import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useUser } from '../../context/UserContext';
import { Form, Button, Alert, Container, Row, Col } from 'react-bootstrap';

function LoginForm({ onLogin }) {
  const navigate = useNavigate();
  const { login } = useUser();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = (event) => {
    event.preventDefault();
    if (!username || !password) {
      setError('Bdna nazwa uytkownika lub haso');
    } else {
      login({ username, password });
      navigate('/dashboard');
    }
  };

  return (
    <Container className="d-flex align-items-center justify-content-center bg-light" style={{ minHeight: "100vh" }}>
      <Row className="w-100">
        <Col md={6} lg={6} className="mx-auto">
          <Form onSubmit={handleSubmit} className="p-4 shadow-sm rounded bg-body">  
            <h2 className="text-center">Zaloguj si</h2>
            <Form.Group className="mb-3" controlId="username">
              <Form.Label>Nazwa uytkownika:</Form.Label>
              <Form.Control
                type="text"
                placeholder="Wpisz nazw uytkownika"
                value={username}
                onChange={(e) => setUsername(e.target.value)} />
            </Form.Group>

            <Form.Group className="mb-3" controlId="password">
              <Form.Label>Haso:</Form.Label>
              <Form.Control
                type="password"
                placeholder="Wpisz haso"
                value={password}
                onChange={(e) => setPassword(e.target.value)} />
              <Form.Text id="passwordHelpBlock" muted>
                Twoje haso musi skada si przynajmniej z omiu znakw, w tym jednego znaku specjalnego oraz cyfry.
              </Form.Text>
            </Form.Group>

            {error && <Alert variant="danger">{error}</Alert>}
            
            <Button type="submit" className="w-100 btn-lg btn-primary">Zaloguj si</Button>  

            <Form.Group className="text-center">
              Nie masz konta? <Link to="/signup">Zarejestruj si</Link>
            </Form.Group>

          </Form>
        </Col>
      </Row>
    </Container>
  );
}

export default LoginForm;
