import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { BrowserRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
import LoginForm from './components/login/LoginForm';
import SignUpForm from './components/login/SignUpForm';
import Dashboard from './components/dashboard/Dashboard';
import UserProfile from './components/profile/UserProfile';
import { UserProvider, useUser } from './context/UserContext';
import ManageOrganizations from './components/organization/ManageOrganizations';
import AddOrganization from './components/organization/AddOrganization';
import EditOrganization from './components/organization/EditOrganization';
import ManageAircraft from './components/aircraft/ManageAircraft';
import AddAircraft from './components/aircraft/AddAircraft';
import EditAircraft from './components/aircraft/EditAircraft';
import Layout from './components/layout/Layout';

function RequireAuth({ children, redirectTo, roles }) {
  const { user } = useUser();
  const hasRequiredRole = roles ? roles.some(role => user?.roles.includes(role)) : true;
  
  return user && hasRequiredRole ? <Layout>{children}</Layout> : <Navigate to={redirectTo} />;
}

function App() {
  return (
    <UserProvider>
      <Router>
        <Routes>
          <Route path="/login" element={<LoginForm />} />
          <Route path="/signup" element={<SignUpForm />} />
          <Route path="/" element={<Navigate replace to="/login" />} />
          <Route path="/profile" element={<RequireAuth redirectTo="/login"><UserProfile /></RequireAuth>} />
          <Route path="/dashboard" element={<RequireAuth redirectTo="/login"><Dashboard /></RequireAuth>} />
          <Route path="/manage" element={<RequireAuth redirectTo="/login" roles={['ADMIN']}><ManageOrganizations /></RequireAuth>} />
          <Route path="/edit-organization/:id" element={<RequireAuth redirectTo="/login" roles={['ADMIN']}><EditOrganization /></RequireAuth>} />
          <Route path="/add-organization" element={<RequireAuth redirectTo="/login" roles={['ADMIN']}><AddOrganization /></RequireAuth>} />
          <Route path="/manage-aircraft" element={<RequireAuth redirectTo="/login" roles={['ADMIN', 'MECHANIC']}><ManageAircraft /></RequireAuth>} />
          <Route path="/add-aircraft" element={<RequireAuth redirectTo="/login" roles={['ADMIN', 'MECHANIC']}><AddAircraft /></RequireAuth>} />
          <Route path="/edit-aircraft/:id" element={<RequireAuth redirectTo="/login" roles={['ADMIN', 'MECHANIC']}><EditAircraft /></RequireAuth>} />
        </Routes>
      </Router>
    </UserProvider>
  );
}

export default App;
