import React from 'react';
import { useNavigate } from 'react-router-dom';
import { LinkContainer } from 'react-router-bootstrap';
import { useUser } from '../../context/UserContext';
import { Navbar, Nav, Button, Container} from 'react-bootstrap';

const AppNavbar = () => {

    const { logout } = useUser();
    const navigate = useNavigate();

      const handleLogout = () => {
        logout();
        navigate('/login');
      };


  return (
    <Container fluid className="px-3">
       <Navbar bg="light" expand="lg" className="px-3 mb-5">
              <LinkContainer to="/dashboard">
                <Navbar.Brand href="/login">Panel uytkownika</Navbar.Brand>
              </LinkContainer>
              <Nav className="me-auto">
                <LinkContainer to="/manage">
                  <Nav.Link>Zarzdzanie organizacjami</Nav.Link>
                </LinkContainer>
                <LinkContainer to="/manage-aircraft">
                <Nav.Link>Zarzdzanie samolotami</Nav.Link>
                </LinkContainer>
                </Nav>
                <Nav className="ms-auto">
                <LinkContainer to="/profile">
                  <Nav.Link>Edycja profilu</Nav.Link>
                </LinkContainer>
                <Button variant="outline-primary" onClick={handleLogout} className="ms-auto">Wyloguj</Button>
              </Nav>
          </Navbar>
    </Container>
  );
};

export default AppNavbar;
