import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Form, Button, Container, Row, Col } from 'react-bootstrap';

function SignUpForm() {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    organization: '',
    position: '',
    role: '',
  });

  const [organizations, setOrganizations] = useState([]);
  const [positions, setPositions] = useState([]);

  useEffect(() => {
    fetch('/api/maintenance/positions')
      .then(response => {
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        return response.json();
      })
      .then(data => {
        setPositions(data);
      })
      .catch(error => {
        console.error('There was an error fetching the roles!', error);
      });
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  
  const handleSubmit = (e) => {
    e.preventDefault();
    // obsluga wysylki
    console.log('Form data submitted:', formData);
  };

  return (
    <Container className="d-flex align-items-center justify-content-center bg-light" style={{ minHeight: "100vh" }}>
      <Row className="w-100">
        <Col md={6} lg={6} className="mx-auto">
          <Form onSubmit={handleSubmit} className="p-4 shadow-sm rounded bg-body">
            <h2 className="text-center">Rejestracja nowego konta</h2>

            <Form.Group className="mb-3" controlId="name">
              <Form.Label>Nazwa uytkownika:</Form.Label>
              <Form.Control
                name="name"
                type="text"
                placeholder="Wpisz nazw uytkownika"
                value={formData.name}
                onChange={handleChange} />
            </Form.Group>

            <Form.Group className="mb-3" controlId="password">
              <Form.Label>Haso:</Form.Label>
              <Form.Control
                name="password"
                type="password"
                placeholder="Wpisz haso"
                value={formData.password}
                onChange={handleChange} />
            </Form.Group>

            <Form.Group className="mb-3" controlId="email">
              <Form.Label>Adres e-mail:</Form.Label>
              <Form.Control
                name="email"
                type="email"
                placeholder="Wpisz adres e-mail"
                value={formData.email}
                onChange={handleChange} />
            </Form.Group>

            <Form.Group className="mb-3" controlId="organization">
              <Form.Label>Organizacja:</Form.Label>
              <Form.Control
                as="select"
                name="organization"
                value={formData.organization}
                onChange={handleChange}
              >
                <option value="">Wybierz swoj organizacje:</option>
                {organizations.map(organization => (
                  <option key={organization} value={organization}>{organization}</option>
                ))}
              </Form.Control>
              </Form.Group>

              <Form.Group className="mb-3" controlId="position">
              <Form.Label>Stanowisko:</Form.Label>
              <Form.Control
                as="select"
                name="position"
                value={formData.position}
                onChange={handleChange}
              >
                <option value="">Wybierz swoje stanowisko:</option>
                {positions.map(position => (
                  <option key={position} value={position}>{position}</option>
                ))}
              </Form.Control>
            </Form.Group>

            <Button type="submit" className="w-100 btn-lg btn-primary">Zarejestruj si</Button>
            <Form.Group className="text-center mt-3">
              Masz ju konto? <Link to="/login">Zaloguj si</Link>
            </Form.Group>

          </Form>
        </Col>
      </Row>
    </Container>
  );
}

export default SignUpForm;
